.first-address {
  padding-bottom: 50px;

  border-bottom: 1px solid $basic-light-grey;
}

.first-address__list {
  @include reset-list;
}

.first-address__item {
  margin-bottom: 5px;

  color: $special-black;

  &:last-child {
    margin-bottom: 0;
  }
}

@media screen and (max-width: 767px) {
  .first-address__list {
    margin-bottom: 30px;
  }
}
