.address {
  @include reset-list;

  margin-top: 70px;
}

.address__item-title {
  font-weight: 600;
  color: $special-black;
}

.address__item {
  display: grid;

  grid-template-columns: 1fr 1fr;
}

.address__item-subtitle {
  color: $special-grey;
  text-align: right;

  & a {
    color: $special-grey;
    text-decoration: none;

    &:hover {
      color: $special-grey;

      opacity: 0.7;
    }

    &:focus {
      outline: none;
      opacity: 0.7;
    }
  }
}
